/*Custom Scrollbar*/
.custom-scrollbar {
  scrollbar-width: thin; /* For Firefox */
  scrollbar-color: #888 #f1f1f1;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 12px; /* Adjust scrollbar width */
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px; /* Rounded track edges */
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px; /* Rounded thumb edges */
  border: 3px solid #f1f1f1; /* Adds space around the thumb for rounded cap effect */
}

/* Create rounded caps on top and bottom */
.custom-scrollbar::-webkit-scrollbar-thumb:before,
.custom-scrollbar::-webkit-scrollbar-thumb:after {
  content: "";
  display: block;
  height: 6px; /* Half of scrollbar width for perfect circular caps */
  width: 6px;
  background: #888;
  border-radius: 50%; /* Fully rounded circle */
  margin: auto;
}

/* Place the cap at the top of the thumb */
.custom-scrollbar::-webkit-scrollbar-thumb:before {
  position: absolute;
  top: -6px; /* Negative offset to position the cap above */
  left: 50%;
  transform: translateX(-50%);
}

/* Place the cap at the bottom of the thumb */
.custom-scrollbar::-webkit-scrollbar-thumb:after {
  position: absolute;
  bottom: -6px; /* Negative offset to position the cap below */
  left: 50%;
  transform: translateX(-50%);
}

